.btn-sm {
    font-size: 10px !important;
    padding: 3px !important;
}

.btn-lg {
    font-size: 20px;
    padding: 10px 20px;
}

.btn {
    font-size: 12px;
    padding: 6px 12px;
    border: none;
    border-radius: 6px;

    background: #d9d9d9;
    cursor: pointer;
}

.btn:hover {
    transition: 0.2s;
    background: rgba(217, 217,217, 0.5);
    outline: none;
}

.btn-primary {
    color: white;
    background: #416cff;
}
.btn-primary:hover {
    transition: 0.2s;
    background: #224ad9;
}

.btn-success {
    color: white;
    background: #1da11d;
}

.btn-success:hover {
    transition: 0.2s;
    background: #008600;
}


